<script>
import Bus from "../../../utils/Bus";

export default {
  name: "AccountInfoPlatform",
  destroyed() {
    Bus.$off('globalMsgForPlatformUser');
  },
  data() {
    let Cui_realname = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('真实姓名不可为空'));
      } else {
        callback();
      }
    };
    let LoginId = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('用户名不可为空'));
      } else {
        callback();
      }
    };
    let Password = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('密码不可为空'));
      } else {
        callback();
      }
    };
    let Phone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号'))
      } else if (!this.isCellPhone(value)) {
        callback(new Error('请输入正确的手机号!'))
      } else {
        callback()
      }
    };

    return {
      plat_clmShow: false,
      plat_ballShow: false,
      menuLeft: '-180',  //偏移量
      menuLeft2: '80',  //球在左边菜单偏移量
      menuWidth: '80px',//菜单宽度   菜单宽度增加多少 偏移量同步增加多少
      floatDivWidth: 70, //悬浮窗宽度
      floatRightValue: '50px',  //悬浮窗位置右起
      floatTopValue: '55px',//悬浮窗位置上起
      ballIcon: 'el-icon-more',
      cardBodyHeight: true,
      cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
      icon1: "el-icon-d-arrow-left",
      icon2: 'el-icon-caret-top',
      tableSize: 19,//默认用户表格宽度
      multipleSelection: [],//table选中行内容
      tableData: [],//表格数据
      delLoginId: [],//缓存需要删除的登录标识
      sortOrderCol: '',

      //用户机构页面 使用参数
      realNameByOrg: '',//用户真实名称
      loginIdByOrg: '',//登录标识
      userIdByOrg: '',//用户ID
      checkOrgId: [],//点击时选择的OrgId
