        this.cardBodyStyle = 'padding-top:0px;padding-bottom:0px;';
        this.icon2 = 'el-icon-caret-bottom';
      }
      //  console.info("confCardBodyHeigth:"+this.cardBodyHeight);
    },
    clearQuery(formName) {
      this.$refs[formName].resetFields();
    },
    /**  表格列排序设置   */
    sortColumn(column) {
      //  console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        //  console.info("属性名称："+ column.prop+",排序方式："+column.order);

        this.sortOrderCol = column.prop + " " + orderFlag;
        //  console.info("用户选择排序列："+this.sortOrderCol);
        this.queryValveCurrentData();
      } else {
        this.sortOrderCol = '';
      }

    },
    /**------分页-------start----*/
    handleSizeChange(val) {
      //  console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.queryValveCurrentData();
    },
    handleCurrentChange(val) {
      //  console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val;
      this.queryValveCurrentData();
    },
    /**------分页------end--*/

    /** 页面table复选框 */
    handleSelectionChange(val) {
      this.multipleSelection = val;
      //console.log("=====table=checkBox======="+JSON.stringify(this.multipleSelection))
    },
    /** 页面table复选框选中事件 */
    isSelect(row, index) {
      if (row.cui_org_type != '0' && row.cui_login_id !== 'admin') {
        return true;
      }
      return false;
    },
    /** 删除使用 */
    getRowKeys(row) {
      return row.cui_login_id;
    },
    /**  打开悬浮球   */
    openBox() {
      let isDrag = document.getElementById("dragbtn").getAttribute("data-flag");
      if (isDrag == true) {
        return;
      }

      if (this.ballIcon === 'el-icon-more') {
        this.ballIcon = 'el-icon-right';
      } else {
        this.ballIcon = 'el-icon-more';
      }

      var x = this.$refs['ballBtn'].$el.getBoundingClientRect().x;
      var w = document.documentElement.clientWidth;
      //console.log('点击事件x:'+x+",w:"+w/2);
      this.plat_ballShow = !this.plat_ballShow;
      if (x > (w / 2)) {//大于窗口一半
        document.getElementById("buttonPlatfromRole").style.left = this.menuLeft + 'px';
      } else {//小于窗口一半
        document.getElementById("buttonPlatfromRole").style.left = Math.abs(this.menuLeft2) + 'px';// '70px';
      }
    },


    /** -----------------菜单权限树使用------------------------- */

    /** treeTable 全选方法 */
    tableTreeSelectAll() {//选择全部及不选择
      this.tableCheckedKeys = !this.tableCheckedKeys;

      this.spliteTable(this.tableTreeData, this.tableCheckedKeys);
      let table_arr = this.$refs.platRoleTreeTable.selection;
      // console.info("All方法选择的行："+JSON.stringify(table_arr) );

      if (table_arr.length === 0) {
        this.tableRowIdMap.clear();
      } else {
        for (let i = 0; i < table_arr.length; i++) {
          this.tableRowIdMap.set(table_arr[i].id, true);
        }
      }
      this.menuNum = this.tableRowIdMap.size;
      //console.info("选中行："+JSON.stringify(this.tableRowIdMap) );
    },

    /** 处理数据  */
    spliteTable(rows, flag) {
      for (let i = 0; i < rows.length; i++) {
        // console.info("spliteTable_i:"+i);
        this.$refs.platRoleTreeTable.toggleRowSelection(rows[i], flag);
        rows[i].status = flag;

        //this.tableRowIdMap.set(rows[i].id,flag===null?true:false);
        if (this.tableRowIdMap.has(rows[i].id) === false) {
          this.tableRowIdMap.set(rows[i].id, true);
        } else if (this.tableRowIdMap.get(rows[i].id) === true) {
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id);
        } else {
          this.tableRowIdMap.set(rows[i].id, true, rows[i].caption);
        }

        if (rows[i].detail) {
          this.spliteTable(rows[i].detail, flag);
        }

      }
      // console.info("spliteTable_tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    },

    /** 点击当前行checkbox事件 （select事件） */
    tableTreeSingleSelect(selection, row) {
      if (this.tableRowIdMap.has(row.id) === false) {
        this.tableRowIdMap.set(row.id, true);
      } else if (this.tableRowIdMap.get(row.id) === true) {
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id);
      } else {
        this.tableRowIdMap.set(row.id, true);
      }
      //  console.info("tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
      //  console.info("tableSingleSelect_row:"+JSON.stringify( row ) );
      if (row.detail) {
        this.spliteTable(row.detail);
      }
    },

    /** 权限树初始化 */
    initPlatTableTreeData(permission) {
      if (this.permissionType === 'all') {
        this.tableTreeData = [
          {
            caption: '银行用户权限',
            id: '00-0',//特殊标识
            detail: permission.heatconfig.permissionconfig.detail
          },
          {
            caption: '平台用户权限',
            id: '00-1',//特殊标识
            detail: permission.platfromconfig.permissionconfig.detail
          }

        ];
      } else {
        this.tableTreeData = permission.permissionconfig.detail;
      }
      this.tableDataClone = this.tableTreeData;
      console.log('测试1：' + JSON.stringify(permission.heatconfig.permissionconfig.detail))
      console.log('测试2：' + JSON.stringify(permission.platfromconfig.permissionconfig.detail))
      // console.log('测试3：' + JSON.stringify(permission.permissionconfig.detail))
    },

    /** 当选项发生改变时触发 */
    tableTreeSelectChange(val) {
      this.menuNum = this.tableRowIdMap.size;
      // console.log("-change-选择的所有ID-"+JSON.stringify(this.tableRowIdMap))
    },

    /** 重新渲染treeTable */
    initLoopCheckTable(rows) {//循环检测选中状态
      for (let i = 0; i < rows.length; i++) {
        // console.info("initLoopCheckTable_i:"+i+",id:"+rows[i].id+",status:"+rows[i].status);
        if (rows[i].status === 'false') {
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id);
          this.$refs.platRoleTreeTable.toggleRowSelection(rows[i], false)
        } else if (rows[i].status === 'true') {
          this.tableRowIdMap.set(rows[i].id, true);

          this.$refs.platRoleTreeTable.toggleRowSelection(rows[i], true)
        }
        if (rows[i].detail) {
          this.initLoopCheckTable(rows[i].detail);
        }
      }

    },

    /** 将菜单权限树初始化 将数据中 status为false的移除 */
    initTreeData(row) {
      for (let i = 0; i < row.length; i++) {
        if (row[i].detail) {
          this.initTreeData(row[i].detail);
        }
        if (row[i].status === 'false') {
          row.splice(i, 1);
          i--;
        }
      }
      return row;
    },

    /** 清空treeTable的复选框 */
    noCheckTreeTable(rows) {//循环检测选中状态
      this.tableRowIdMap = new Map();
      for (let i = 0; i < rows.length; i++) {
        this.$refs.platRoleTreeTable.toggleRowSelection(rows[i], false)
        rows[i].status = false;
        if (rows[i].detail) {
          this.noCheckTreeTable(rows[i].detail);
        }
      }
    },
    /***
     * 回显使用
     * @param curId 当前选中的节点ID
     * @param sourceData tableData数据对象
     * @param targetData 新增加或修改的对象内容
     * @param layer 1是同级，2是子级
     * @param flag 将status修改成true
     *
     */
    checkPermissionId(curId, sourceData, targetData, layer, flag) {
      // console.info("进入checkPermissionId:"+sourceData)
      // console.info("查询选中ID:"+curId);
      var result = false;
      for (var i1 = 0; i1 < sourceData.length; i1++) {
        //  console.info("当前id:"+sourceData[i1].id);
        if (flag === '5') {
          // console.info('查询表格status内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          // console.info("query find status id:"+sourceData[i1].id);
          if (sourceData[i1].id === curId) {
            sourceData[i1].status = 'true';
            //   console.info("modify status find ok");
            this.tableDataClone = this.tableData;
            //   console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          } else {
            if (sourceData[i1].hasOwnProperty("detail")) {
              //    console.info("query status not find 递归查询detail")
              result = this.checkPermissionId(curId, sourceData[i1].detail, targetData, layer, flag);
              if (result === true) {
                break;
              }
            } else {
              //    console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },

    /** 将用户权限点赋予到tableData */
    initTableDataPermissionStatus(permissionKey, tableData) {
      for (let k of Object.keys(permissionKey)) {
        this.checkPermissionId(k, tableData,
          {},
          '0',
          '5');
      }
      //初始化渲染
      this.$nextTick(() => {
        this.initLoopCheckTable(tableData);
      })
    },

    /** --------------机构权限处理方法------------------------*/

    /** 权限树弹出层 */
    showOrgTree(row) {

      this.platformUserOrgVisible = true;
      this.userIdByOrg = row.cui_uuid;
      this.realNameByOrg = row.cui_realname;
      this.loginIdByOrg = row.cui_login_id;
      if (this.checkAllOrgIds.length > 0) {
        this.$nextTick(() => {
          this.noCheckOrgTree(this.checkAllOrgIds);
        })
      }
      this.checkOrgId = [];
      this.checkAllOrgIds = [];
      this.selType = '';
      this.queryUserOrgIds(row.cui_login_id);
    },

    /** 机构树点击checkbox事件 */
    handleCheck(data, node) {
      //  console.log("check事件"+ JSON.stringify(data), node);
      //  console.log("check事件"+ JSON.stringify(data), JSON.stringify( node.checkedKeys) );
      this.checkOrgId = node.checkedKeys;
      if (node.checkedNodes.length > 0) {
        this.selType = node.checkedNodes[0].type;
        // console.log("====机构树checkBox选中事件====查询级别============="+JSON.stringify(node.checkedNodes))
      }
      this.checkAllOrgIds = [];
      this.recursion(this.treeData, false)
      // this.treeCheckedKeys = node.checkedKeys;
      this.checkAllOrgIds = this.checkAllOrgIds.reverse();

      // console.log("====机构树checkBox选中事件====选中的ID============="+node.checkedKeys)
    },

    /** 查询选中的机构ID的所有父节点 */
    recursion(arr, isChild) {
      let aCheck = [];
      for (let i = 0; i < arr.length; i++) {
        let obj = arr[i];
        aCheck[i] = false;
        if (obj.detail) {
          aCheck[i] = this.recursion(obj.detail, true) ? true : aCheck[i];
          if (aCheck[i]) {
            this.checkAllOrgIds.push(obj['id']);
          }
        }
        for (let j = 0; j < this.checkOrgId.length; j++) {
          if (obj['id'] == this.checkOrgId[j]) {
            aCheck[i] = true;
            if (this.checkAllOrgIds.indexOf(obj['id']) == -1) {
              this.checkAllOrgIds.push(obj['id']);
            }
            break;
          }
        }
      }
      if (isChild) {
        return aCheck.indexOf(true) != -1;
      }
    },

    /** 清空tree的复选框 */
    noCheckOrgTree(rows) {
      this.$refs.OrgTree.setCheckedKeys(rows)
    },

    /** 查询用户的组织机构ID数组 */
    queryUserOrgIds(userLoginId) {
      let _jsondata_ = {
        username: userLoginId,
      };
      //  console.info(JSON.stringify(_jsondata_));
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryOrganTemplate_new,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        // console.log("异步调用成功返回:"+JSON.stringify(res));
        // 清空已选择的机构树数组值，下方需要重新赋值，防止数据重复
        this.userQueryOrgIds = [];
        if (res.data.status == 'success') {
          //处理组织机构ID
          let orgKey = JSON.parse(res.data.orgkey);
          let i1 = 0;
          for (let ids of Object.keys(orgKey)) {
            this.userQueryOrgIds[i1] = ids;
            i1++
            //  console.log("===用户机构ID查询事件=====查询到机构ID============="+i1);
          }
          // console.log("===回显用户机构ID======"+this.userQueryOrgIds)
          // 用户组织机构赋权回显
          this.$refs.OrgTree.setCheckedKeys(this.userQueryOrgIds, true);
        }
      }, error => {
        //  console.log("异步调用出错:"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '当前用户机构查询出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      }).catch((res) => {
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
        this.$alert(JSON.stringify(res.message), '当前用户机机构查询查询出错', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      });
    },

    /** 提交平台用户权限标识 */
    submitUserOrg() {
      if (this.checkAllOrgIds.length == 0) {
        this.$alert("您还未选择机构信息，无需提交！", '保存平台用户机构信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }

      if (this.selType < this.customGlobal.userInfoExt.cui_org_type) {
        this.$alert("您还未取得此机构的权限！", '保存平台用户机构信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }
      let objOrgId = {};
      for (let i = 0; i < this.checkAllOrgIds.length; i++) {
        objOrgId[this.checkAllOrgIds[i]] = true
      }
      //保存用户机构权限
      this.saveUserOrgKeys(this.loginIdByOrg, objOrgId);
      //保存平台用户查询级别
      this.saveUserOrgType(this.loginIdByOrg, this.selType);
      //清空用户的选择
      this.$nextTick(() => {
        this.$refs.OrgTree.setCheckedKeys(this.checkAllOrgIds);
      });
      this.userOrgId = [];
      this.platformUserOrgVisible = false;
      //  console.log("==========用户权限选择============"+this.userOrgId);
    },

    /** 保存用户机构权限 */
    saveUserOrgKeys(loginId, objOrgId) {
      let _jsondata_ = {
        username: loginId,
        orgkey: objOrgId
      };
      const loading = this.$loading({
        lock: true,
        text: '操作中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });
      //  console.info("===保存用户机构权限=====提交参数==="+JSON.stringify(_jsondata_));
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
