    /** treeTable 全选方法 */
    tableBtnTreeSelectAll() {//选择全部及不选择
      this.tableBtnCheckedKeys = !this.tableBtnCheckedKeys;

      this.spliteBtnTable(this.tableBtnTreeData, this.tableBtnCheckedKeys);
      let table_arr = this.$refs.platBtnTreeTable.selection;
      //console.info("All方法选择的行："+JSON.stringify(table_arr) );

      if (table_arr.length === 0) {
        this.tableBtnRowIdMap.clear();
      } else {
        for (let i = 0; i < table_arr.length; i++) {
          this.tableBtnRowIdMap.set(table_arr[i].id, true);
        }
      }
      this.btnNum = this.tableBtnRowIdMap.size;
    },

    /** 处理数据  */
    spliteBtnTable(rows, flag) {
      for (let i = 0; i < rows.length; i++) {
        // console.info("spliteTable_i:"+i);
        this.$refs.platBtnTreeTable.toggleRowSelection(rows[i], flag);
        rows[i].status = flag;

        //this.tableRowIdMap.set(rows[i].id,flag===null?true:false);
        if (this.tableBtnRowIdMap.has(rows[i].id) === false) {
          this.tableBtnRowIdMap.set(rows[i].id, true);
        } else if (this.tableBtnRowIdMap.get(rows[i].id) === true) {
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableBtnRowIdMap.delete(rows[i].id);
        } else {
          this.tableBtnRowIdMap.set(rows[i].id, true, rows[i].caption);
        }

        if (rows[i].detail) {
          this.spliteBtnTable(rows[i].detail, flag);
        }

      }
      // console.info("spliteTable_tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    },

    /** 点击当前行checkbox事件 （select事件） */
    tableBtnTreeSingleSelect(selection, row) {
      if (this.tableBtnRowIdMap.has(row.id) === false) {
        this.tableBtnRowIdMap.set(row.id, true);
      } else if (this.tableBtnRowIdMap.get(row.id) === true) {
        // this.tableRowIdMap.set(row.id,false);
        this.tableBtnRowIdMap.delete(row.id);
      } else {
        this.tableBtnRowIdMap.set(row.id, true);
      }
      //  console.info("tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
      //  console.info("tableSingleSelect_row:"+JSON.stringify( row ) );
      if (row.detail) {
        this.spliteBtnTable(row.detail);
      }
    },

