            i++
            // console.log("===用户权限ID查询事件=====查询到的权限ID============="+p1);
          }
          this.menuNum = i;
          // 用户菜单权限回显 先清空 后赋值
          this.noCheckTreeTable(this.tableTreeData)
          this.initTableDataPermissionStatus(this.userQueryRoleIds, this.tableTreeData);
        }
      }, error => {
        //  console.log("异步调用出错:"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '权限查询出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      }).catch((res) => {
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
        this.$alert(JSON.stringify(res.message), '权限查询查询出错', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      });
    },

    reQueryValveCurrentData() {
      this.queryParam.currentPage = 1;
      this.queryValveCurrentData();
    },
    /** 查询方法 */
    queryValveCurrentData() {
      let that = this;
      let _jsondata_ = {
        perPageSize: this.queryParam.pageSize,
        currentPage: this.queryParam.currentPage,
        cui_login_id: this.platUserSearchForm.cui_login_id,
        cui_realname: this.platUserSearchForm.cui_realname,
        cui_concat_phone: this.platUserSearchForm.cui_concat_phone,
        cui_status: this.platUserSearchForm.cui_status,
        cui_writetime: this.platUserSearchForm.cui_writetime,
        query_type: this.customGlobal.userInfoExt.cui_org_type,
      };

      if (this.sortOrderCol !== '') {
        _jsondata_.orderby = this.sortOrderCol;
      } else {
        _jsondata_.orderby = 'cui_writetime desc';
      }
      //   console.info(JSON.stringify(_jsondata_));
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });

      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.platformAccount_query_new,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        // console.log("异步调用成功返回:"+JSON.stringify(res));
        if (res.data.status == 'success') {

          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;

        } else {
          //  console.info("查询失败");
          loading.close();
          that.tableData = [];
          // this.$alert('登录已超时，请重新登录！', '', {
          //   confirmButtonText: '确定',
          //   callback: action => {
          //     //  console.info('data:'+JSON.stringify(res.data));
          //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
          //       this.$router.replace({path: '/login'});
          //     }
          //   }
          // });
          if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
            this.$router.replace({path: '/login'});
          }
        }
      }, error => {
        //  console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },

    /** 平台用户新增 */
    platformUserAdd() {
      //获取角色信息
      if (this.roleSelect.length == 0) {
        this.getPlatformRole();
      }
      if (this.reLoad != '') {
        this.$nextTick(() => {
          // this.$refs['platformEdit'].resetFields();
          this.noCheckTreeTable(this.tableTreeData);
          this.initTableDataPermissionStatus('{}', this.tableTreeData);
        });

      }
      this.passwdShow = true;
      this.platformUserFormVisible = true;
      this.tableDialgTitle = "新增平台用户及菜单授权";
      this.tableOperFlag = 1;
      this.menuNum = 0;
      this.platformUserForm.cui_login_id = '';
      this.platformUserForm.cui_password = '123456';
      this.platformUserForm.cui_realname = '';
      this.platformUserForm.cui_concat_phone = '';
      this.platformUserForm.cui_uuid = '';
      this.platformUserForm.r_memo = '';
      this.platformUserForm.cui_status = true;
      this.roleId = '';

    },

    /** 平台用户编辑 */
    platformUserEdit(row) {

      this.passwdShow = false;
      this.platformUserFormVisible = true;
      this.tableDialgTitle = "编辑平台用户及菜单授权";
      this.tableOperFlag = 2;
      this.platformUserForm.cui_login_id = row.cui_login_id;
      this.platformUserForm.cui_realname = row.cui_realname;
      this.platformUserForm.cui_concat_phone = row.cui_concat_phone;
      this.platformUserForm.cui_uuid = row.cui_uuid;
      this.platformUserForm.cui_status = row.cui_status == 1 ? true : false;
      //获取回显用的菜单树ID 同一个页面中，两次打开同一个用户的dialog时不更新菜单树
      // console.log("======编辑用户ID与上次ID==========="+this.reLoad,row.cui_login_id)
      if (this.reLoad != row.cui_login_id) {
        this.queryUserRoleIds(row.cui_login_id);
      }
      //获取角色信息
      if (this.roleSelect.length == 0) {
        this.getPlatformRole();
      }
    },

    /** 平台用户删除 */
    platformUserDel(row) {

      this.tableOperFlag = 3;
      this.$confirm('请再次确认是否要删除用户[<span style="color:red;">' + row.cui_realname + '</span>]的记录?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {

        let _jsondata_ = {
          uid: row.cui_uuid,
          login_id: row.cui_login_id
        };
        const loading = this.$loading({
          lock: true,
          text: '操作中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, .7)'
        });
        //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.platformAccount_delete_new,// 方法路径
          this.customGlobal.serverUrl.orgBaseURI,//服务器地址
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        //post提交后返回值操作
        p1.then((res) => {
          //  console.log("异步调用成功返回:"+JSON.stringify(res));
          this.queryValveCurrentData();
          loading.close();
          if (res.data.status == 'success') {
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          } else {
            //  console.info("删除失败");
            loading.close();
            // this.$alert('登录已超时，请重新登录！', '', {
            //   confirmButtonText: '确定',
            //   callback: action => {
            //     //  console.info('data:'+JSON.stringify(res.data));
            //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
            //       this.$router.replace({path: '/login'});
            //     }
            //   }
            // });
            if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
              this.$router.replace({path: '/login'});
            }
          }
        }, error => {
          //  console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '删除出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          //  console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '删除出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });

    },

    /** 重置表单 */
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    /** 关闭新增用户dialog */
    closeBox() {
      this.reLoad = this.platformUserForm.cui_login_id;
      //console.log("===关闭新增用户dialog====UID==="+this.platformUserForm.cui_login_id)
      this.platformUserFormVisible = false;
      this.$refs['platformEdit'].clearValidate();
    },

    /** 电话号校验 */
    isCellPhone(val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false
      } else {
        return true
      }
    },

    /** 获取平台的角色ID与名称 */
    getPlatformRole() {
      let _jsondata_ = {
        r_type: 1
      };
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.platformRole_queryAll_new,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        //  console.log("异步调用成功返回:"+JSON.stringify(res));
        if (res.data.status == 'success') {
          this.roleSelect = res.data.rows;
        } else {
          //  console.info("角色获取失败");
        }
      }, error => {
        //  console.log("异步调用出错:"+JSON.stringify(error));
      }).catch((res) => {
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
      });
    },

    /** 批量删除用户 */
    delPlatformUserArray() {
      if (this.multipleSelection.length == 0) {
        this.$alert('您还未选择要删除的用户！', '提示信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }
      let loginIds = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        loginIds[i] = this.multipleSelection[i].cui_login_id;
      }
      this.$confirm('请再次确认是否要对已选中的[<span style="color:red;">' + loginIds.length + '</span>]个用户信息进行删除', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {

        let _jsondata_ = {
          login_id: loginIds
        };
        const loading = this.$loading({
          lock: true,
          text: '操作中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, .7)'
        });
        //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.delUserByArray_new,// 方法路径
          this.customGlobal.serverUrl.orgBaseURI,//服务器地址
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        //post提交后返回值操作
        p1.then((res) => {
          loading.close();
          if (res.data.status == 'success') {
            this.queryValveCurrentData();
            //删除成功后清空之前选中数据
            this.$refs.platformUserTable.clearSelection();
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          } else {
            //  console.info("批量删除失败");
            loading.close();
            // this.$alert('登录已超时，请重新登录！', '', {
            //   confirmButtonText: '确定',
            //   callback: action => {
            //     //  console.info('data:'+JSON.stringify(res.data));
            //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
            //       this.$router.replace({path: '/login'});
            //     }
            //   }
            // });
            if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
              this.$router.replace({path: '/login'});
            }
          }
        }, error => {
          //  console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '批量删除出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          //  console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '批量删除出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
      //  console.log("=====多选的ID========"+this.multipleSelection+"====="+this.multipleSelection.length);
    },

    /**  重置用户密码 */
    resetUserPasswd(row) {
      this.$confirm(' 请再次确认是否要重置 [ <span style="color:red;">' + row.cui_realname + '</span> ] 的密码，重置后密码为:  <span style="color:red;">' + 123456 + '</span> ', '操作提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,

      }).then(() => {
        let _jsondata_ = {
          login_id: row.cui_login_id,
          pass_word: this.customGlobal.md5('123456')
        };
        const loading = this.$loading({
          lock: true,
          text: '操作中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, .7)'
        });
        //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.resetUserPasswd_new,// 方法路径
          this.customGlobal.serverUrl.orgBaseURI,//服务器地址
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        //post提交后返回值操作
        p1.then((res) => {
          //  console.log("异步调用成功返回:"+JSON.stringify(res));
          loading.close();
          if (res.data.status == 'success') {
            this.$message({
              type: 'success',
              message: '重置密码成功!'
            });
          } else {
            //  console.info("重置密码失败");
            loading.close();
            // this.$alert('登录已超时，请重新登录！', '', {
            //   confirmButtonText: '确定',
            //   callback: action => {
            //     //  console.info('data:'+JSON.stringify(res.data));
            //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
            //       this.$router.replace({path: '/login'});
            //     }
            //   }
            // });
            if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
              this.$router.replace({path: '/login'});
            }
          }
        }, error => {
          //  console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '重置密码出错!', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        }).catch((res) => {
          //  console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '重置密码出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },


    /** 表单提交 */
    submitForm(formName) {
      let url_method = '';
      let msg_show = '';
      let flag = this.tableOperFlag;
      switch (flag) {
        case 1:
          url_method = this.customGlobal.serverUrl.platformAccount_add_new;
          msg_show = "添加";
          break;
        case 2:
          url_method = this.customGlobal.serverUrl.platformAccount_update_new;
          msg_show = "编辑";
          break;
      }
      //将存储RoleID的map转换为数组
      var arrRole = new Array();
      let objPermissionId = {};
      let i = 0;
      this.tableRowIdMap.forEach(function (value, key) {
        arrRole[i] = key;
        objPermissionId[key] = true;
        i++;
      });
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let _jsondata_ = {
            cui_uuid: this.platformUserForm.cui_uuid,
            cui_login_id: this.platformUserForm.cui_login_id,
            cui_password: this.customGlobal.md5(this.platformUserForm.cui_password),
            cui_realname: this.platformUserForm.cui_realname,
            cui_concat_phone: this.platformUserForm.cui_concat_phone,
            cui_status: this.platformUserForm.cui_status == true ? '1' : '2',
            plat_org: arrRole,
            permission_id: objPermissionId
          };
          console.log("12234:" + this.platformUserForm.cui_uuid)
          console.log('_jsondata_:' + JSON.stringify(_jsondata_));
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',
            background: 'rgba(0,0,0,0.7)'
          });
          //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
          var p1 = this.customGlobal.postHttpClient(
            url_method,// 方法路径
            this.customGlobal.serverUrl.orgBaseURI,//服务器地址
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
          );
          //post提交后返回值操作
          p1.then((res) => {
            //  console.log("异步调用成功返回:"+JSON.stringify(res));
            this.queryValveCurrentData();
            if (res.data.status == 'success') {
              this.platformUserFormVisible = false;
              //提交之后 清空表单及系统公用变量值，防止数据出错
              this.tableRowIdMap = new Map();
              this.noCheckTreeTable(this.tableTreeData);
              this.queryValveCurrentData();
              loading.close();
              this.$message({
                type: 'success',
                message: msg_show + '成功!'
              });
            } else {
              //  console.info("编辑失败");
              loading.close();
              // this.$alert('登录已超时，请重新登录！',   '', {
              //   confirmButtonText: '确定',
              //   callback: action => {
              //     //  console.info('data:'+JSON.stringify(res.data));
              //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
              //       this.$router.replace({path: '/login'});
              //     }
              //   }
              // });
              if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
                this.$router.replace({path: '/login'});
              }
            }
          }, error => {
            loading.close();
            //  console.log("异步调用出错:"+JSON.stringify(error));
          }).catch((res) => {
            loading.close();
            //  console.log("异步调用失败返回:"+JSON.stringify(res));
          });

        } else {
          //  console.log('error submit!!');
          return false;
        }
      });
    },


  },

}

</script>

<style scoped>
/*<style >*/

.account-plat-card1 /deep/ .el-card__header {
  /*background:#f5f5f5 !important;*/
  background: #f2f3f8 !important;
  /*height: 10px!important;*/
  padding: 10px !important;
}

.account-plat-card2 /deep/ .el-card__body {
  padding-top: 5px !important;
  padding-bottom: 5px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
}

.account-plat-formitem /deep/ .el-form-item {
  margin-bottom: 3px !important;
}

.account-plat-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
  word-break: break-all !important;
  margin-left: 0px !important;
}


.account-plat-fixed-right /deep/ .el-table__fixed-right {
  height: 100% !important;
}

.account-plat-fixed /deep/ .el-table__fixed {
  height: 100% !important;
}

</style>
